import Products from "../../models/product.js";
import cancel from "../../../img/cancel.png";

class List  {
  constructor() {
    this.model = new Products();
  }

  async getData() {
      return new Promise(resolve => this.model.getProductsList().then(data => resolve(data)));
  }

  render(productsData) {
      return new Promise(resolve => {
      resolve(`
              ${this.getItemsHtml(productsData)}
                    `);
    });
  }

  afterRender(){
    this.setActions();
  }

  setActions() {
      const tg = window.Telegram.WebApp;
      const contentContainerInner = document.getElementsByClassName('content-container__inner')[0];
      contentContainerInner.addEventListener('click', event => this.setAddButtonsActions(event.target, tg))
    }

  getItemsHtml(productsData) {
    const html = [];

     productsData.forEach((product, index) => {
      const imgUrl = this.imageExists(product.img) ? product.img : cancel;

      html.push(
      `
      <div class='item'>
       <img class="img" src=${imgUrl} alt="item_img">
      <div class='text-container'>
        <p>${product.name} <br> <span class="bold">${product.countPoints}</span> </p>
    </div>
        <button class="add-btn" id="${product.id}" data-name="${product.name}"></button>
      </div>
      `
      )
    })

    return html.join('')
  }

  imageExists(image_url){
    const http = new XMLHttpRequest();
    try{
      http.open('HEAD', image_url, false);
      http.send();
    }catch (e) {
      return false
    }

    return http.status != 404;
  }

    setAddButtonsActions(target, tg) {
      switch (true) {
          case(target.classList.contains('add-btn')):
             tg.showAlert(' ')
              this.model.sendNewOrderData({
                  //     
                  chatId: '123',
                  queryId:  '123',
                  // chatId: tg?.initDataUnsafe?.user.id ?  tg?.initDataUnsafe?.user.id : '123',
                  // queryId:  tg?.initDataUnsafe?.query_id ?  tg?.initDataUnsafe?.query_id : '123',
                  bonusProductId: target.id
              }).then((res) => {
                  tg.showAlert(` `)
                  tg.showAlert(`${JSON.stringify(res)}`)
                  tg.close()
              }).catch(e => tg.showAlert(e))

              break;
      }
    }
}


export default List;
