  async sendNewOrderData(data) {
    try {
      const response = await fetch(`${endPoints.apiUrl}/order`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json;charset=utf-8'
        },
        body: JSON.stringify(data)
      });

      return response
    }catch (e) {
      return e
    }
  }

